html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: auto;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  max-width: 98%;
  height: 100%;
  background-color: #f0f1f1;
}
#root {
  width: 98%;
  margin: auto;
}
.error {
  background-color: #a61e65
}
/****** header ******/

.App-header {
  height: 11vh;
  width: 98%;
  position: fixed;
  z-index: 1001;
  border-bottom: 5px solid #4c3f54;
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: 100%;
  background-repeat: no-repeat;
  background-image: url(/night-spot-app/static/media/party2.a9bb7a23.jpg);
}
h1 {
  color: white;
}
h1, h2, h3 {
  text-align: center;
  margin: 0;
  text-transform: uppercase;
}
.new-error {
  padding-top: 35%;
  color: #322136;
}
hr {
  background: #427e8f;
  height: 1px;
  margin: 0;
  border: none;
  width: 100%;
}
ul {
  padding: 9px;
  list-style: none
}
select {
  font-family: inherit;
  border-radius: 4px;
  margin-left: 5px;
}
#select-menu {
  font-size: 75%;
}
/****** main content ******/

/*skip link
Code from Udacity Aria lessons
*/

.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  background-position: 0%;
  color: white;
  padding: 8px;
  z-index: 100;
}
main {
  border-top: 1px solid #d9d9d9;
  height: 87vh;
  position: fixed;
  width: 98%;
  margin: 0;
  top: 11%;
}
#map-container {
  border-top: 1px solid #d9d9d9;
  height: 38%;
  position: fixed;
  width: 98%;
  margin: 0;
  top: 12%;
}
#map {
  width: 100%;
  height: 100%;
}
.gm-style .gm-style-iw {
  white-space: pre-line;
  color: #322136;
  font-weight: normal;
}
/****** sidebar ******/

section h2 {
  color: #322136;
}
section {
  border-top: 1px solid #fff;
  width: 98%;
  border-bottom: 1px solid #d9d9d9;
  height: 49%;
  position: fixed;
  top: 48%;
  background-color: #f0f1f1;
}
#sidebar {
  height: 100%;
}
#sidebar .hours {
  height: auto;
  overflow: hidden;
  letter-spacing: 0.3px;
  padding-bottom: 5px;
}
.filter {
  position: fixed;
  border-bottom: 1px solid #d9d9d9;
  height: 59px;
  border-top: 1px solid #d9d9d9;
  width: 98%;
  text-align: center;
  color: #322136;
  background-color: #4a476d2b;
}
.results-container {
  margin: 0;
  padding-top: 61px;
  height: 79%;
}
.venue-photo {
  max-width: 100%;
  height: auto;
  display: block;
  margin: 0;
  padding: 9px;
  margin-left: auto;
  margin-right: auto;
  width: 80%;
}
.attr-container {
  text-align: right;
  padding: 2px 17px 0px 193px;
  height: 21px;
}
.attr-container-iw {
  border: 2px solid #a61e6580;
  height: 12px;
  width: 131px;
  margin-top: 4px;
  left: 0px;
  top: 16px;
}
object {
  color: #a61e6580;
  position: relative;
  bottom: 16px;
}
.drop-down-menu {
  text-align: left;
  color: #322136;
  width: 100%;
}
#sidebar ul {
  position: absolute;
  overflow: auto;
  height: 66%;
  margin: 0;
  padding-bottom: 0;
  width: 95%;
}
.list-items {
  box-shadow: 2px -1px 10px 0px rgba(0, 0, 0, 0.23);
  padding: 1px 0 3px 0px;
  margin-bottom: 7px;
  border-radius: 5px;
}
li a {
  padding-left: 9px;
  font-size: 14px;
}
td {
  padding-right: 11px;
}
.more-details {
  background-color: #4c3f54;
  border-bottom: 0 solid #eee;
  color: #fff;
  display: inline-block;
  font-size: 12px;
  margin: 0;
  margin-top: 3px;
  padding: 5px 6px 4px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  border-radius: 0px 0px 0px 5px;
}
.address {
  font-size: 14px;
  line-height: 1rem;
  margin-top: 6px;
  padding-left: 6px;
}
table {
  padding: 9px;
}
.hours-list, .rating {
  line-height: 1.1em;
  font-size: 14px;
}
.rating {
  margin: 4px 2px 5px;
  padding-left: 6px;
}
.rating span {
  color: #a61e65
}
/****** footer ******/

footer {
  background-color: #353a49d4;
  color: #DEDEDE;
  font-size: 8pt;
  letter-spacing: 1px;
  padding: 0;
  text-align: center;
  text-transform: uppercase;
  bottom: 0px;
  position: fixed;
  width: 98%;
  height: 17px;
}
/****** Responsiveness ******/

@media screen and (min-width: 475px) {
  main {
    border-top: 4px solid #fff;
    height: 76vh;
    top: 113px;
    width: 100%
  }
  .App-header {
    height: 19vh;
    width: 100%
  }
  h1 {
    font-size: 9vw;
  }
  #map-container {
    border-top: 1px solid #d9d9d9;
    background: #eee none repeat scroll 0 0;
    height: 78%;
    position: fixed;
    right: 0px;
    width: 50%;
    margin: 0;
    top: 19%;
    left: 50%;
  }
  #map {
    border-top: 1px solid #d9d9d9;
    background: #eee none repeat scroll 0 0;
    height: 100%;
    right: 0px;
    width: 100%;
    margin: 0;
  }
  section {
    width: 49%;
    border-top: none;
    height: 79%;
    top: 19%;
  }
  .filter {
    width: 49%;
    height: 39px;
    padding: 10px 0px 33px 0;
  }
  #sidebar {
    border-right: 2px solid #d9d9d9;
    width: 100%;
  }
  #sidebar ul {
    height: 74%;
  }
  .results-container {
    padding-top: 85px;
    height: 81%;
  }
  footer {
    width: 100%
  }
  #root {
    width: 100%
  }
}
@media screen and (min-width: 768px) {
  section h3 {
    padding-bottom: 3px;
  }
  section {
    width: 35%;
    height: 70%;
    border: 50px solid #f0f1f1;
    border-bottom: 0;
  }
  #sidebar {
    border: 2px solid #d9d9d9;
  }
  #sidebar ul {
    height: 70%;
  }
  .filter {
    width: 35%;
    padding-top: 7px;
    padding-bottom: 36px;
  }
}
@media screen and (min-width: 800px) {
  section h3 {
    padding-bottom: 3px;
  }
  section {
    width: 37%;
    border: 50px solid #f0f1f1;
  }
  #sidebar {
    border: 2px solid #d9d9d9;
  }
  .filter {
    width: 37%;
    padding-top: 7px;
    padding-bottom: 36px;
  }
}
@media screen and (min-width: 1000px) {
  main {
    height: 83vh;
  }
  #map-container {
    width: 60%;
    left: 40%;
  }
  section {
    width: 26%;
    height: 72vh;
    border-left: 65px solid #f0f1f1;
    border-right: 65px solid #f0f1f1;
    top: 17%
  }
  .filter {
    width: 26%;
  }
  #sidebar {
    height: 100%;
  }
  #sidebar ul {
    height: 71%;
  }
  .results-container {
    height: 80%;
  }
}
